.stock {
	padding-top: 149px;
	padding-bottom: 50px;

	@include vp-tablet {
		padding-top: 93px;
	}

	@include vp-mobile {
		padding-top: 66px;
		padding-bottom: 50px;
	}

	h2 {
		font-family: $ff-alumni;
		text-align: center;
		font-weight: 600;
		font-size: 54px;
		line-height: 1;
		letter-spacing: 0.03em;
		margin-bottom: 34px;

		@include vp-tablet {
			font-size: 42px;
			margin-bottom: 50px;
		}

		@include vp-mobile {
			font-size: 32px;
		}
	}
}

.stock__list {
	padding: 0;
	margin: 0;
	list-style: none;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: auto auto;
	gap: 40px;

	@include vp-tablet {
		gap: 30px;
	}

	@include vp-mobile {
		grid-template-columns: 1fr;
		gap: 31px;
	}
}

.stock__item:not(.stock__item--accent) {
	color:#1C3374;
	border: 7px solid transparent;
	border-image: linear-gradient(#d10a42, #00359f);
	border-image-slice: 1;
	padding-top: 25px;
	padding-left: 28px;
	padding-right: 28px;
	padding-bottom: 25px;

	@include vp-tablet {
		flex: 0 0 47.5%;
		min-height: 211px;
		display: flex;
		flex-direction: column;
		padding-bottom: 30px;
		align-items: flex-start;
	}

	@include vp-mobile {
		max-width: none;
		flex: 0 0 100%;
		align-items: center;
		padding-top: 36px;
		padding-bottom: 40px;
	}

	p {
		font-weight: 400;
		font-size: 16px;
		margin: 0;
		margin-bottom: 64px;

		@include vp-tablet {
			margin-bottom: 0;
			text-align: left;
		}

		@include vp-mobile {
			text-align: center;
		}
	}

	a {
		font-weight: 700;
		font-size: 12px;
		line-height: 14px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color:#1C3374;
		border-bottom: 1px solid #1C3374;
		transition: color 0.3s ease 0s,
		border 0.3s ease 0s;

		@include vp-tablet {
			margin-top: auto;
		 }

		&:hover,
		&:focus,
		&:active {
		  color: #ED0233;
		  border-bottom: 1px solid #ED0233;
		}
	 }
}

.stock__item--accent {
	color: $color-text-light;
	background-color: #ed0233;
	grid-row: 1/-1;
	grid-column: 1/2;
	display: flex;

	@include vp-tablet {
		grid-row: 1/2;
		grid-column: 1/-1;
	}

	@include vp-mobile {
		grid-row: 1/2;
		grid-column: 1/-1;
		flex-direction: column;
	}
}

.stock__item-image {
	aspect-ratio: 280/462;
	min-width: 280px;

	@include vp-tablet {
		aspect-ratio: 399/462;
		min-width: 399px;
	}

	@include vp-mobile {
		min-width: 100%;
		aspect-ratio: 290/299;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.stock__item-text {
	padding-top: 34px;
	padding-left: 45px;
	padding-right: 10px;
	padding-bottom: 41px;
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	@include vp-tablet {
		width: 41%;
		padding: 34px 10px 42px 44px;
	}

	@include vp-mobile {
		width: 100%;
		padding-left: 50px;
		padding-right: 50px;
		padding-bottom: 44px;
		align-items: center;
	}

	h3 {
		font-weight: 700;
		font-size: 28px;
		line-height: 156%;
		margin: 0;
		margin-bottom: 21px;
		display: flex;
		flex-direction: column;

		@include vp-mobile {
			align-items: center;
			margin-bottom: 16px;
		}
	}

	span {
		font-weight: 700;
		font-size: 70px;
		line-height: 88%;
	}

	p {
		font-weight: 400;
		font-size: 18px;
		color: $color-default-white;
		line-height: 1.4;
		margin: 0;
		margin-bottom: 188px;

		@include vp-tablet {
			text-align: left;
		}

		@include vp-mobile {
			text-align: center;
			margin-bottom: 29px;
		}
	}

	a {
		font-weight: 700;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $color-text-light;
		margin-top: auto;
		border-bottom: 1px solid $color-text-light;
		width: fit-content;
		padding-bottom: 3px;
		transition: color 0.3s ease 0s,
		border 0.3s ease 0s;

		&:hover,
		&:focus,
		&:active {
			color: $color-accent-dark;
			border-bottom: 1px solid $color-accent-dark;
		}

		@include vp-mobile {
			padding-bottom: 0;
		}
	}
}
