.coaches {
  padding: 30px 30px 93px;
  display: flex;
  justify-content: center;

  @include vp-tablet {
    padding-bottom: 103px;
    padding-left: 101px;
    padding-right: 101px;
    padding-top: 31px;
  }

  @include vp-mobile {
    padding-left: 47px;
    padding-right: 47px;
    padding-bottom: 70px;
    padding-top: 30px;
  }
}

.coaches__container {
  width: 90%;
  max-width: 1160px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include vp-tablet {
    width: 100%;
  }
}

.coaches__item {
  background-color: rgba(20, 101, 214, 0.745);
  position: relative;
  aspect-ratio: 260/294;

  @include vp-tablet {
    aspect-ratio: 268/294;
  }
}

.coaches__image {
  position: absolute;
  top: 0;
  left: 0;

  &::before {
    content: attr(data-coach);
    color: $color-text-light;
    font-family: $ff-roboto;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.5;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 25px;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.coaches__content {
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  color: $color-text-light;
  z-index:3;
  transform: scale(0);
  transition: 0.3s ease 0s;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 10%;padding-left: 26px;padding-right: 26px;padding-top: 21%;
  overflow: hidden;


  @include vp-mobile {
    padding-top: 19%;
  }


  &::before{
    content:'';
    background-color: rgba(16, 38, 84, 0.89);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: disc;
    padding-left: 45px;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;

    @include vp-tablet {
      padding-left: 44px;
    }

    @include vp-mobile {
      padding-left: 24px;
    }
  }

  p {
    font-family: $ff-roboto;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    color: #b6bdcc;
    margin-top: 0;
    margin-bottom: 15%;

    @include vp-tablet {
      line-height: 1;
      margin-top: 0;
      margin-bottom: 18%;
    }

    @include vp-mobile {
      margin: 0 0 23%;
    }
  }

  li {
    font-family: $ff-roboto;
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    color: $color-default-white;
    margin-bottom: 15%;

    &:last-child {
      margin-bottom: 0;
    }

    @include vp-tablet {
      margin-bottom: 14%;
    }

    @include vp-mobile {
      margin-bottom: 16%;
    }
  }
}

.coaches__content ul::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.coaches__content li::marker {
  color: $color-default-white;
  list-style-type: disc;
}

.coaches__item:hover .coaches__content {
  transform: scale(1);
}

.coaches__item:hover .coaches__image::before {
  opacity: 0;
}
